.fileExplorer {
  padding: 20px 40px;
  height: calc(100vh - var(--header-height) - 40px);
  width: calc(100vw - 80px);
}

.folderList {
  display: flex;
  flex-direction: row;
  gap: 20px;
  align-items: baseline;
}

.header {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 10px 10px 40px 10px;
}